body {
 
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  background-image: url('./Assests/cool-background.svg');
  background-size: 150%;
  background-repeat: no-repeat;
  background-blend-mode: lighten;
  background-color: rgba(240, 248, 255, 0.712)
}

button {
  padding: 10px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: rgb(18, 12, 44);
  color: white;
  border-radius: 8px;
  cursor: pointer;
  
}


h3, h4 {
  margin-bottom: 0;
}

nav {
  text-align: center;
  position: relative;
  top: 0;
  width: 100%;
  margin-bottom: -50px;
  
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 25px;
  padding-bottom: 0px;
  padding-right: 5px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 25px;
}

nav a:hover {
  
  color: rgb(109, 109, 109);
  
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
  
  padding-bottom: 20px;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 1px 0 0 0;
  
}

.article-author {
  
  font-size: 15px;
  color: rgb(0, 0, 0);
  font-style: italic;
  font-weight: bold;
  float: right;
  padding-right: 50px;
}


.under-substring {
  font-size: 15px;
  color: rgb(0, 0, 0);
  padding-bottom: 30px;
}

.article-page-author {
  margin-top: 8px;
  font-size: 15px;
  margin-bottom: 0px;
  font-style: italic;
  color: rgb(104, 104, 104);
  position: relative;
}

.article-page-title{
  margin-bottom: -25px;
  color: rgb(0, 0, 0);
}

.article-list-all{
  color: rgb(255, 255, 255);
}

.article-page-content{
  word-wrap: break-word;
  color: rgb(0, 0, 0);
  width: 100%;
  padding-top: -20px;
}

.article-page-likes{
  font-size: 15px;
  color: rgb(121, 121, 121);
  margin-top: 0px;
  margin-bottom: 0px;
}

.article-list-likes {
  
  font-size: 15px;
  color: rgb(121, 121, 121);
  
  
}

.line {
  border-bottom: 1px solid rgb(0, 0, 0);
  padding-top: 20px;
  padding-bottom: 20px;
}

#add-comment-form {
  border-bottom: 1px solid rgb(0, 0, 0);
  padding-bottom: 20px;
}

#add-post-form {
  
  padding: 4px;
}

#add-comment-form h3 {
  
  margin-top: 50px;
  margin-bottom: 16px;
}

#add-post-form h2 {
  margin-bottom: 16px;
  text-align: center;
  
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 3px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 1px solid black;
  margin-bottom: 16px;
  opacity: .85;
}

#add-comment-form textarea {
  width: 300px;
}

#add-post-form input, textarea {
  display: block;
  width: 675px;
  padding: 8px;
  border-radius: 3px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 1px solid black;
  margin-bottom: 16px;
  opacity: .85;
}

#add-post-form textarea {
  height: 500px;
}

#add-comment-form button {
  width: 100px;
  
}

#add-post-form button {
  width: 200px;
  justify-content: center;
  margin-bottom: 50px;

}

#upvotes-section button, p {
  display: inline-block;
  
}

.comment {
  border-bottom: 1px solid rgb(197, 197, 197);
  padding: 16px;
}
